
import { ref, onMounted, defineComponent } from "vue";

export default defineComponent({
  name: "Stepper",
  props: ["activeIndex", "items"],
  methods: {
    isStepActive(index: number): string {
      if (this.activeIndex === index) {
        return "active";
      } else {
        return "";
      }
    },
  },
});
