
import { ref, onMounted, defineComponent } from "vue";

export default defineComponent({
  name: "ProgressBar",
  props: ["loading"],
  data() {
    return {
      isLoading: this.loading,
      widthPercentage: 20,
      interval: 0,
      isDirectionLTR: true, //left to right or right to left movement
      isIncreasing: true,
    };
  },
  mounted() {
    this.interval = window.setInterval(() => {
      if (
        this.isDirectionLTR &&
        this.isIncreasing &&
        this.widthPercentage < 100 && this.widthPercentage > 0
      ) {
        this.widthPercentage = this.widthPercentage + 1;
      } else if (
        this.isDirectionLTR &&
        this.isIncreasing &&
        this.widthPercentage == 100
      ) {
        this.isDirectionLTR = false;
        this.isIncreasing = false;
        this.widthPercentage = 99;
      } else if (
        !this.isDirectionLTR &&
        !this.isIncreasing &&
        this.widthPercentage < 100 && this.widthPercentage > 0
      ) {
        this.widthPercentage = this.widthPercentage - 1;
      } else if (
        !this.isDirectionLTR &&
        !this.isIncreasing &&
        this.widthPercentage == 0
      ) {
        this.isIncreasing = true;
        this.widthPercentage = 1;
      } else if (
        !this.isDirectionLTR &&
        this.isIncreasing &&
        this.widthPercentage > 0 && this.widthPercentage < 100
      ) {
        this.widthPercentage = this.widthPercentage + 1;
      } else if (
        !this.isDirectionLTR &&
        this.isIncreasing &&
        this.widthPercentage == 100
      ) {
        this.isDirectionLTR = true;
        this.isIncreasing = false;
        this.widthPercentage = 99;
      } else if (
        this.isDirectionLTR &&
        !this.isIncreasing &&
        this.widthPercentage < 100 && this.widthPercentage > 0
      ) {
        this.widthPercentage = this.widthPercentage - 1;
      } else if (
        this.isDirectionLTR &&
        !this.isIncreasing &&
        this.widthPercentage == 0
      ) {
        this.widthPercentage = 1;
        this.isIncreasing = true;
      }
    }, 10);
  },
  unmounted() {

    window.clearInterval(this.interval);
  },
});
