
import { defineComponent } from "vue";
import SideNav from "@/components/home/SideNav.vue";
import FormEditor from "@/components/edit/FormEditor.vue";
import EntityService from "@/services/EntityService";
import ConfirmDialog from "primevue/confirmdialog";
import MemberEditor from "@/components/edit/MemberEditor.vue";
import { IM } from "@/vocabulary/IM";
import LoggerService from "@/services/LoggerService";

export default defineComponent({
  name: "Editor",
  components: {
    SideNav,
    ConfirmDialog,
    FormEditor,
    MemberEditor
  },
  beforeRouteLeave(to, from, next) {
    if (this.checkForChanges()) {
      this.$confirm.require({
        message:
          "All unsaved changes will be lost. Are you sure you want to proceed?",
        header: "Confirmation",
        icon: "pi pi-exclamation-triangle",
        accept: () => {
          next();
        }
      });
    } else {
      next();
    }
  },
  computed: {
    hasMembers(): any {
      return IM.HAS_MEMBERS in this.concept ? true : false;
    }
  },
  data() {
    return {
      iri: this.$route.params.iri?.toString(),
      concept: {} as any,
      conceptOriginal: {} as any,
      conceptUpdated: {} as any,
      membersOriginal: {} as any,
      membersUpdated: {} as any,
      active: 0,
      contentHeight: ""
    };
  },
  async mounted() {
    this.$nextTick(() => {
      window.addEventListener("resize", this.setContentHeight);
    });
    this.fetchConceptData();

    this.setContentHeight();
  },
  beforeUnmount() {
    window.removeEventListener("resize", this.setContentHeight);
  },
  methods: {
    async fetchConceptData(): Promise<void> {
      if (this.iri) {
        await EntityService.getEntity(this.iri)
          .then(res => {
            this.concept = res.data;
          })
          .catch(err => {
            this.$toast.add(
              LoggerService.error("Editor get concept request failed", err)
            );
          });

        await EntityService.getEntityDefinitionDto(this.iri)
          .then(res => {
            this.conceptOriginal = res.data;
            this.conceptUpdated = JSON.parse(JSON.stringify(res.data));
          })
          .catch(err => {
            this.$toast.add(
              LoggerService.error("Editor get concept request failed", err)
            );
          });

        if (this.hasMembers) {
          await EntityService.getEntityMembers(this.iri, false, false)
            .then(res => {
              this.membersOriginal = res.data;
              this.membersUpdated = JSON.parse(JSON.stringify(res.data));
            })
            .catch(err => {
              this.$toast.add(
                LoggerService.error(
                  "Editor get concept members request failed",
                  err
                )
              );
            });
        }
      }
    },

    submit(): void {
      console.log("submit");
    },

    updateMembers(data: any) {
      this.membersUpdated = data;
    },

    updateConcept(data: any) {
      this.conceptUpdated = data;
    },

    checkForChanges() {
      if (
        JSON.stringify(this.membersUpdated) ===
          JSON.stringify(this.membersOriginal) &&
        JSON.stringify(this.conceptUpdated) ===
          JSON.stringify(this.conceptOriginal)
      ) {
        return false;
      } else {
        return true;
      }
    },

    refreshEditor(): void {
      this.conceptUpdated = {};
      this.membersUpdated = {};
    },

    setContentHeight(): void {
      const container = document.getElementById(
        "editor-main-container"
      ) as HTMLElement;
      const header = container.getElementsByClassName(
        "p-panel-header"
      )[0] as HTMLElement;
      const nav = container.getElementsByClassName(
        "p-tabview-nav"
      )[0] as HTMLElement;
      const buttonBar = container.getElementsByClassName(
        "button-bar"
      )[0] as HTMLElement;
      const content = container.getElementsByClassName(
        "p-panel-content"
      )[0] as HTMLElement;
      const currentFontSize = parseFloat(
        window
          .getComputedStyle(document.documentElement, null)
          .getPropertyValue("font-size")
      );
      if (
        container &&
        header &&
        nav &&
        currentFontSize &&
        buttonBar &&
        content
      ) {
        header.style.border = "none";
        header.style.borderBottom = "1px solid #dee2e6";
        content.style.border = "none";
        content.style.paddingBottom = "0";
        const height =
          container.getBoundingClientRect().height -
          header.getBoundingClientRect().height -
          nav.getBoundingClientRect().height -
          buttonBar.getBoundingClientRect().height -
          currentFontSize * 3 -
          2;
        this.contentHeight = "height: " + height + "px;";
      }
    }
  }
});
